@media (max-width: 560px) {
  .contact__details {
    flex-direction: column;
    align-items: flex-start;
  }
  .contact__details a {
    padding: 10px;
    font-size: 1rem;
  }
}

.details__item {
  color: #A5A5A5;
  font-weight: 500;
  text-decoration: none;
}

.details__item:first-of-type {
  margin-right: 20px;
}

.details__item img {
  margin-right: 10px;
}

/**
 * Form
 */
.form {
  display: flex;
  flex-direction: column;
}

.form__field {
  display: flex;
  flex-direction: column;
}

.form__field:first-of-type {
  margin-bottom: 42px;
}

.form__label {
  font-weight: bold;
  text-transform: uppercase;
  color: #08A6E4;
  line-height: 0.9rem;
  letter-spacing: 3px;
  font-size: 0.6875rem;
}

.form__input {
  border: none;
  border-bottom: 1px solid #A5A5A5;
  background-color: transparent;
  line-height: 2.625rem;
}

.form__consent {
  margin-top: 30px;
}

.form__checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 1.375rem;
}

.form__checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.form__checkbox .checkbox {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #ffffff;
  border: 1px solid #A5A5A5;
  border-radius: 4px;
}

.form__checkbox input:checked ~ .checkbox {
  background-image: url(../assets/Checkmark.svg);
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
}

.form__checkbox .consent__text {
  margin: 0 10px;
  font-size: 0.75rem;
  color: #A5A5A5;
  line-height: 17px;
}

.form .btn {
  font-size: 0.8125rem;
  padding: 20px 97px;
  align-self: flex-end;
  margin-top: 20px;
}

.footer {
  box-shadow: 0 -5px 40px #00000029;
  padding: 20px 0;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.footer__copy {
  font-size: 0.6875rem;
  line-height: 1.5rem;
  color: #A5A5A5;
  margin-top: 6px;
}

.footer__icons a:first-of-type {
  margin-right: 25px;
}

/**
 * Statistics
 */
.statistics {
  margin-top: 100px;
}

.statistics__title {
  text-align: center;
}

.statistics__container {
  max-width: 800px;
  margin: 100px auto 120px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 560px) {
  .statistics__container {
    flex-direction: column;
    align-items: center;
  }
}

.statistics__single {
  max-width: 160px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
  font-family: "Raleway", sans-serif;
}
@media (max-width: 560px) {
  .statistics__single {
    padding: 10px 0;
  }
}

.statistics__circle {
  border-radius: 50%;
  width: 63px;
  height: 63px;
  position: relative;
}

.statistics__img {
  position: absolute;
}

.statistics__single:nth-child(1) .statistics__circle {
  background-color: #FB3B64;
}

.statistics__single:nth-child(1) .statistics__img {
  top: 0;
  left: 45%;
}

.statistics__single:nth-child(2) .statistics__circle {
  background-color: #08A6E4;
}

.statistics__single:nth-child(2) .statistics__img {
  top: 14px;
  left: 47%;
}

.statistics__single:nth-child(3) .statistics__circle {
  background-color: #55DFB4;
}

.statistics__single:nth-child(3) .statistics__img {
  top: 6px;
  left: 50%;
}

.statistics__number {
  font-size: 1.25rem;
  font-weight: 800;
  letter-spacing: 0.1875rem;
  margin-top: 13px;
}

.statistics__description {
  color: #A5A5A5;
  margin-top: 19px;
}

@media (max-width: 560px) {
  .statistics.container {
    display: flex;
    flex-direction: column;
  }
}